<template>
    <input type="hidden" :value="visitor_id">
</template>


<script>
import FingerprintJS from '@fingerprintjs/fingerprintjs';


export default {
    name: "finger_print",
    data(){
        return {
            visitor_id: '',
            fpPromise: '',
        }
    },
    methods: {
        get_finger_print_promise(){
            const fpPromise = FingerprintJS.load();
            fpPromise
                .then(fp => fp.get())
                .then(result => {
                    return result.visitorId;
                });
            this.fpPromise = fpPromise;
        },
        get_finger_print_from_promise: async function(){
            const fp = await this.fpPromise;
            const result = await fp.get();
            this.visitor_id = result.visitorId;
        }
    },
    mounted(){
        this.get_finger_print_promise();
        this.get_finger_print_from_promise();
    }
}
</script>
